@import '../../utils/styles/mixins';

.articleCard {
  width: 400px;
  height: 530px;

  // background: #ffffff;
  border: 1px solid #eaeaea;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
  border-radius: 5px;

  @include onSmallMobile {
    width: 340px;
  }

  &__image {
    width: 400px;
    height: 217px;

    @include onSmallMobile {
      width: 340px;
    }
  }
}
